	export default {
		data() {
			return {
				infos:{},
			}
		},
		onShow() {
			uni.getUserInfo({
				success(ret) {
					console.log(ret);
					uni.setStorageSync('userInfo', ret.userInfo);
					uni.setStorageSync('userAllInfo', ret)
				}
			});	
		},
		onLoad() {
          this.infos=uni.getStorageSync('userInfo')
		},
		methods: {
			toGoTicekets(n) {
				uni.navigateTo({
					url: '/pages/Ticekets/Ticekets'
				})
			},
			toGoaddr() {
			this.$pub_fun.open_map();
			},
			toGoTel() {
				this.$pub_fun.Call_Tel()
			}
		}
	}
